import { getparam } from '~/networking/run';
import styles from "./PreviewSurvey.module.css";
import { useParams } from 'react-router-dom';
import React from 'react';

function PreviewSurvey({ mode = "online" }) {
  const resolveMode = (mode) => {
    if (mode == "offline") return "offline";
    return "online";
  };
  const withEmbeddedParam = () => {
    const surveyId = getparam(useParams(), "surveyId");
    let location = `/preview-survey/${surveyId}`;
    if (location.indexOf("?mode") == -1) {
      location = location + "?mode=" + resolveMode(mode);
    } else {
      location = location.replace(
        "?mode=offline",
        `?mode=${resolveMode(mode)}`
      );
      location = location.replace(
        "?mode=online",
        `?mode=${resolveMode(mode)}`
      );
    }
    return location;
  };

  return (
    <div className={styles.container}>
      {mode == "online" ? (
        <iframe src={withEmbeddedParam()} className={styles.onlinePreview} />
      ) : mode == "online-phone" ? (
        <div className={styles.wrapperMob}>
          <img src="/phone-android.png" className={styles.phoneBg} />
          <iframe src={withEmbeddedParam()} className={styles.offlinePreview} />
        </div>
      ) : (
        <div className={styles.wrapperMob}>
          <img src="/phone-android.png" className={styles.phoneBg} />
          <iframe src={withEmbeddedParam()} className={styles.offlinePreview} />
        </div>
      )}
    </div>
  );
}

export default React.memo(PreviewSurvey);
